import React, { Component } from 'react';
import QueryBuilder from 'react-querybuilder';
import './App.css'

import './bootstrap.min.css'
//import 'react-querybuilder/dist/query-builder.scss'
import queryString from 'qs'
 
const tables = [
  {
    table:'user',
    list:[
      {name: 'id', label: 'User Id'},
      {name: 'firstName', label: 'First Name'},
      {name: 'lastName', label: 'Last Name'},
      {name: 'age', label: 'Age'},
      {name: 'address', label: 'Address'},
      {name: 'phone', label: 'Phone'},
      {name: 'email', label: 'Email'},
    ]
  },
  {
    table:'medsos',
    list:[
      {name: 'id', label: 'Medsos Id'},
      {name: 'twitter', label: 'Twitter'},
      {name: 'facebook', label: 'Facebook' },
    ]
  }
];
const getTables = (tables)=>{
  let _table =[]
  tables.forEach( val =>{
    _table.push(val.table)
  })
  return _table;
}
const fields = ( table ) =>{
  let _table=[];
  table.forEach( val =>{
    if(val.list.length > 0){
      val.list.forEach(val2=>{
        _table.push({
          name:val.table+'.'+val2.name ,label:'('+val.table+')'+val2.label
        })
      })
    }
  })
  return _table;
}

const classConfig={
  addRule:'btn btn-info',
  addGroup:'btn btn-warning',
}
class App extends Component{
  state ={
    query:false,
    saveButtonDisabled:false,
    listErrors:[],
    queryStringGet:false,
    listSelected:[],
  };

  logQuery(query){
    //
    this.setState({
      query
    },()=>{
      if(this.state.query.rules.length > 0){
        this.setState({
          saveButtonDisabled:false
        })
      }else{
        this.setState({
          saveButtonDisabled:false
        })
      }
    });
  }
  
  saveQueryOnSubmit(){
    this.setState({
      saveButtonDisabled:true,
      listErrors:[],
      queryStringGet:false,
    })
    let query = {
      query:this.state.query,
      select:this.state.listSelected,
      from:getTables(tables),
    }
    console.log( this.state.query )
    fetch('//git-api.jouningakure.com/tes/',{
        headers: {
          'Content-Type':'application/x-www-form-urlencoded',
        },
        method:'POST',
        body: queryString.stringify({
          query:JSON.stringify(query),
        })
    })
    .then( res => res.json() )
    .then( res =>{
      if(res.error)
        return Promise.reject(res.error)
      if(res.message)
        this.setState({
          queryStringGet:res.message,
          saveButtonDisabled:false,
        })
    })
    .catch( res =>{
      let save ={
        listErrors:[],
        saveButtonDisabled:false,
      }
      if(res.message){
        save.listErrors.push(res.message)
      }
      this.setState(save)
    })
  }
  handleOnSelect(newInput,oldInput){
    if(typeof this.state.listSelected[oldInput] !== 'undefined' ){
      this.state.listSelected[oldInput] = newInput.target.value
    }
    let baru=[]
    this.state.listSelected.forEach( val =>{
      //if(val === oldInput)val = newInput.target.value
      baru.push(val)
    })
    this.setState({
      listSelected:baru
    })
  }
  handleAddField(){
    let selected = fields(tables)[0].name;
    let baru=[]
    this.state.listSelected.forEach( val =>{
      baru.push(val)
    })
    baru.push(selected);

    this.setState({
      listSelected:baru
    })
  }
  
  handleRemoveSelect(newInput){
    this.state.listSelected.splice( this.state.listSelected.indexOf(newInput) , 1)
    let baru=[]
    this.state.listSelected.forEach( val =>{
        baru.push(val)
    })
    this.setState({
      listSelected:baru
    })
  }

  render(){
    let listErrorsJSX = this.state.listErrors.map( (val,k) =>{
      return <div key={k} className="alert alert-danger">
              <strong>Error : </strong>{ val }
            </div>
    })
    
    return(
      <div className="container">
        <div className="row">
        <div className="col-lg-12">
          <h1>Query Builder</h1>
          <h2>Select</h2>
          <button className="btn btn-default" onClick={this.handleAddField.bind(this)}>Add Fields</button>
          <div>
              {
                this.state.listSelected.length > 0 ?
                <SelectList select={this.state.listSelected} 
                  handleOnSelect={this.handleOnSelect.bind(this)}
                  handleRemoveSelect={this.handleRemoveSelect.bind(this)} /> :
                <span className="label label-info">SELECT (*) </span>
              }
          </div>
          
          <h2>Filter</h2>
          <QueryBuilder 
            fields={fields(tables)}
            onQueryChange={this.logQuery.bind(this)}
            controlClassnames={classConfig}
            />
            
            <div style={{marginTop:'9px'}}>
              <div className="col-lg-12">
              <button onClick={this.saveQueryOnSubmit.bind(this)} className="btn btn-info" disabled={ this.state.saveButtonDisabled } >Generate Query String</button>
              </div>
            </div>
            <div className="clearfix"></div>
            <div style={{marginTop:'9px'}}>
              {listErrorsJSX}
            </div>

            {
              this.state.queryStringGet ? 
                <div style={{marginTop:'9px'}}>
                  <h3>Query yang dihasilkan</h3>
                  <div className="alert alert-success">
                    <strong>Query : </strong>{this.state.queryStringGet}
                  </div>                
                </div> :
                ''
            }
        </div> 
        </div>
        
        
        
        
      </div>
    )
  }
}
 

export default App;

class SelectList extends Component{
  handleOnSelect(old,e){
    if(typeof this.props.handleOnSelect === 'function'){
      this.props.handleOnSelect(e,old);
    }
  }
  handleOnClick(val){
    if(typeof this.props.handleRemoveSelect === 'function'){
      this.props.handleRemoveSelect(val);
    }
  }
  render(){
    let select = this.props.select;
    let listSelect = fields(tables).map(val=>{
      return <option key={val.name} value={val.name} >
              {val.label}              
            </option>
    });
    let listSelected = this.props.select.map((val,k) =>{
      return <div key={k} style={{marginTop:'5px'}}>
              <select onChange={this.handleOnSelect.bind(this,k)} defaultValue={val} >{listSelect}</select>
              <button onClick={this.handleOnClick.bind(this,k)} >&times;</button>
            </div>  
    })
    
    return(
      <div>
      {listSelected}
    </div>
    )
  }
}



